body, html, div {
  font-family: 'Shadows Into Light', cursive;
  font-size: 20px;
}

.App {
  display: -ms-flexbox;
  display: flex;
  width: 100vw;
  height: 100vh;
  color: white;
}

.events {
  -ms-flex-preferred-size: 20%;
      flex-basis: 20%;
  background: #222222;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.characters {
  background: #000000;
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.character {
  -ms-flex-preferred-size: 16.6666%;
      flex-basis: 16.6666%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.characterImageContainer {
  -ms-flex-positive: 1;
      flex-grow: 1;
  position: relative;
}

.characterImage, .characterShield {
  position: absolute;
  width: 100%;
  height: 100%;
}

.characterImage {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.characterShield {
  background: url('shield.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.characterName {
  -ms-flex-preferred-size: 20px;
      flex-basis: 20px;
  text-align: center;
}

.selected {
  background: green;
}

.dead {
  background: red;
}

.shamanAnnouncement {
  background: blue;
  display: none;
}

.shamanAnnouncement.visible {
  display: block;
}



